body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-image: -webkit-gradient(linear, left top, right top, from(rgb(85, 178, 221)), to(rgb(16, 43, 119)));
  background-image: -webkit-linear-gradient(left, rgb(85, 178, 221), rgb(16, 43, 119));
  background-image: -o-linear-gradient(left, rgb(85, 178, 221), rgb(16, 43, 119));
  background-image: linear-gradient(to right, rgb(85, 178, 221), rgb(16, 43, 119));
}



